<template>
  <div id="study-search">
    <header>
      <img @click="gohome" src="../../assets/imgs/images/pre.png" />
      <form action="/">
        <van-search
          v-model="inputvalue"
          show-action
          placeholder="请输入大专栏搜索"
        >
          <template #action>
            <div @click="startSearch">搜索</div>
          </template>
        </van-search>
      </form>
    </header>
    <div class="content">
    <SearchItem
    v-for="item in searchList"
    :key="item.id"
    :id="item.id"
    :pic="item.Image"
    :title="item.Title"
    :SecondTitle="item.SecondTitle"
    :freeType="item.freeType"
    :isVipFree="item.isVipFree"
    :type="3"
    :value="inputvalue"
    ></SearchItem>
    </div>
   
  </div>
</template>

<script>
import SearchItem from '@/components/SearchItem.vue';
export default {
  components: { SearchItem },
  name: "StudySearch",
  data() {
    return {
      inputvalue: this.$route.query.value,
      searchList: [],
    };
  },
  methods: {
    gohome() {
      this.$router.back(-1);
    },
    startSearch() {
      this.getData();
    },
    async getData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/Search",
        data: {
          type: 3,
          str: this.inputvalue,
          page: 1,
          limit: 20,
        },
      });
      this.searchList = data.data.data;
    },
  },
};
</script>

<style lang="scss" scoped>
#study-search {
  header {
    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    padding: 5px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    img {
      width: 10px;
      margin-right: 10px;
    }
    form {
      flex: 1;
      font-size: 20px;
    }
  }
  .content {
    margin-top: 50px;
    padding: 0 15px;
  }
}
</style>